<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">预警</span>
    </mu-appbar>-->
    <van-nav-bar
      title="预警"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div v-for="(item,index) in list" :key="index" class="border">
      <div class="body">
        <div class="flexBox paddingBox">
          <div class="title" style="flex:2">
            <span>预警站点</span>
          </div>
          <div class="contentStyle" style="flex:5">
            <span>{{item.warning.warningStation}}</span>
          </div>
        </div>
        <div class="flexBox paddingBox">
          <div class="title" style="flex:2">
            <span>预警时间</span>
          </div>
          <div class="contentStyle" style="flex:5">
            <span>{{item.warning.warningTime}}</span>
          </div>
        </div>
        <div class="flexBox paddingBox">
          <div class="title" style="flex:2">
            <span>预警类型</span>
          </div>
          <div class="contentStyle" style="flex:5">
            <span style="color:red">{{item.warning.warningType}}</span>
          </div>
        </div>
        <div class="flexBox paddingBox">
          <div class="title" style="flex:2">
            <span>预警编号</span>
          </div>
          <div class="contentStyle" style="flex:5">
            <span>{{item.warning.warningId}}</span>
          </div>
        </div>
      </div>
      <div class="bodyContent">
        <div class="flexBox paddingBox">
          <div class="title" style="flex:2">
            <span>预警内容</span>
          </div>
        </div>
        <div class="paddingBox" v-for="(item,index) in item.content" :key="index">
          <div class="flexBox" v-if="item[0].name!==undefined&&item[0].name!==null">
            <div>
              <span>{{item[0].name}}</span>
            </div>
            <div>
              <span style="color:red">{{item[0].value}}</span>
            </div>
          </div>
          <div class="flexBox" v-if="item[0].name===undefined||item[0].name===null">
             <span style="color:grey">
               暂无
             </span>
          </div>
        </div>
      </div>
    </div>
    <div class="flexBox justy" v-show="showButtom===true" @click="seeMore()">
      <span>查看更多+</span>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import WarningContent from "../../../static/js/homeWarning";
export default {
  data() {
    return {
      token: "",
      warningList: [],
      warningCont: [],
      list: [],
      showButtom: false,
      name: "",
      time: "",
      i: 0,
      lengthArr: 0,
      type: ""
    };
  },
  created() {
    var name = this.$route.query.stationName;
    var time = this.$route.query.time;
    var type = this.$route.query.type;
    this.name = name;
    this.time = time;
    this.type = type;
    if (type.length > 1) {
      this.showButtom = true;
      this.lengthArr = type.length;
    } else {
      this.showButtom = false;
    }
    this.token = sessionStorage.getItem("token");
    this.initData(name, time, type[this.i].warningType);
  },
  filters:{
    filter(value) {
      //debugger
       value = Number(value);
      //console.log("====",value);
      if (value !== null) {
        if (typeof value == "number") {
          return value.toFixed(3);
        } else {
          return value;
        }
      } else {
        return "--";
      }
    },
    filterOther(value){
      if (value !== null) {
        if (typeof value == "number") {
          return value.toFixed(3);
        } else {
          return value+"mg/L";
        }
      } else {
        return "--";
      }
    }
  },
  methods: {
    seeMore() {
      var index = this.i+1;
      this.i++;
      if (index === this.lengthArr-1) {
        this.showButtom = false;
      } else {
        this.showButtom = true;
      }
      this.initData(this.name, this.time, this.type[index].warningType);
    },
    async initData(a, b, c) {
      var url = "/warning";
      var station = a;
      var time = b;
      var type = c;
      var res = await AppBace.request(
        url,
        "POST",
        {
          station,
          time,
          type
        },
        this.token
      );
      //   this.warningList = WarningContent.warningContent(res.data.data);
      
      this.warningList = res.data.data;
      this.warningCont = WarningContent.warningContent(
        res.data.data.warningContent
      );
      this.list.push({ warning: this.warningList, content: this.warningCont });
      console.log(res);
    }
  }
};
</script>
<style  scoped>
.flexBox {
  display: flex;
  justify-content: space-between;
  height: 40px;
  align-items: center;
}
.title {
  color: grey;
}
.paddingBox {
  padding: 0 10px;
}
.body {
  background-color: white;
  /* border-bottom: 10px solid rgb(233, 233, 233); */
}
.bodyContent {
  background-color: white;
}
.justy {
  justify-content: center;
  color: #00a1ff;
}
.border{
  border-bottom: 10px solid rgb(245, 245, 245);

}
</style>


